<template>
  <div id="feedback">
    <common-header title="意见反馈"></common-header>
    <MyTextArea placeholder="您在使用的过程中还满意吗？如果您觉得有任何问题，可以向我们反馈，我们会根据您的问题尽快优化我们的产品，谢谢您的支持！"
    	@change = "getValue"/>
    <div class="upload">
      <UploadImg @getIds="getIds" :maxImgNum='3'/>
    </div>

    <div class="finish__btn" :class="{'active': noEmpty}" style="margin-top:0" @click="getCommentSubmit">提交反馈</div>
  </div>
</template>

