    		if(value.length > 0){
    			this.noEmpty = true
    		}else{
    			this.noEmpty = false
    		}
    	},
    	getIds(ids){
    		console.log(ids)
    		this.imgIdList = ids
    	},
    	getReferenceItemList(){
    		let key = '反馈类型'
    		this.$Api.getReferenceItemList(key).then((res)=>{
    			console.log(res)
    			if(res.q.s == 0){
    				
    			}
    		})
    	},
    	
    	
    	getCommentSubmit() {
//  		this.getReferenceItemList()
    		
    		let content = this.content
    		
    		let image = this.imgIdList.join(',')
    		let images = this.imgIdList
    		
    		this.$Api.getFeedbackSubmit(content,image).then((res)=>{
    			console.log(res)
    		})
//				let a = 11
//				let id = this.$route.params.id
//				let comments = []
//
//				let comment = {
//					userId: 0,
//					starNum: this.starIndex,
//					content: this.textValue,
//					imageIds: this.imgIdList
//				}
//				comments.push(comment)
//				this.$Api.CommentSubmit(a, id, comments).then((res) => {
//					console.log(res)
//				})
			},
    }
  }
</script>

<style lang="scss">
  #feedback {
    #textArea {
      padding-bottom: 0;

      textarea {
        height: 4rem;
        padding: 0.4rem;
        border: 1px solid #eee;
        border-radius: 5px;
      }
    }
  }
</style>



